@import "../../../mixins.module.scss";

.footerContainer {
  background: #fff7f7;
  padding: 2% 7%;
}

.footColOne {
  @include x-start;
}

.footColTwo {
  @include x-center;

  // ul {
  //   @include x-center;
  // }
  .footListRow{
    width:90%;
  }

  .listCol{
    @include x-center;
  }

  a {
    text-decoration: none;
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;

    color: #000000;
  }
}

.footColThree {
  @include y-center;

  p {
    font-family: "Inter";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;

    text-align: center;

    color: #000000;
  }

  .iconGroup {
    width: 62%;
    @include x-sb;
  }
}

.copyright {
  margin-top: 4%;
  text-align: center;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #868686;
}


// for mobile portraits
@media (max-width:576px){
  .footerContainer{
    padding: 6% 7% 10% 7%;
  }
  .footColOne{
    @include x-center;
    margin-bottom: 6%;
  
  }
  .footColTwo{
    .footListRow{
      width:100%;
      margin-bottom:6%;
    }

  .listCol{
    margin-bottom:4%;
  }

  }

  .footColThree{
    .iconGroup{
      width:48%;
    }
  }
}