@import "../../../mixins.module.scss";


.itemsContainer {
  width: 100%;
  height: 70vh;
  padding: 0px;
  margin: 0 auto;
  text-align: center;

 

  h1 {
    margin-bottom: 4%;
    font-family: "Inter";
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 50px;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: capitalize;
    color: #000000;
  }

  .viewAll {
    @include x-center;
  }

  a {
    margin-top: 3%;
    font-family: "Karla";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #ff3b00;
  }
}





